import fetch from 'isomorphic-fetch'

export default class {
  static async getDictBookData() {
    const res = await fetch('https://raw.githubusercontent.com/thanglamhuu/ndhpDictionnary/master/OneWord2Dict.json')
    const data = await res.json()
    return data
  }
  static async getDictTruyenData() {
    console.log('getDictTruyenData');
    const res = await fetch('/static/OneWord2Dict.json')
    const data = await res.json()
    return data
  }
}
