import { getMessageError, getErrorData } from "./utils";

const ANALYTICS_TRACKING_ID = "UA-127429119-4";
let tracker;
if (process.browser) {
  tracker = require("react-ga"); // eslint-disable-line global-require
}
export const setupGA = () => {
  if (process.browser && !window.GA_INITIALIZED) {
    initGA();
    window.GA_INITIALIZED = true;
  }
}
export const initGA = () => {
  // console.log("InitGA", ANALYTICS_TRACKING_ID,process.browser , !window.GA_INITIALIZED);
  if (process.browser && !window.GA_INITIALIZED) {
    tracker.initialize(ANALYTICS_TRACKING_ID, { titleCase: false });
    window.GA_INITIALIZED = true;
  }
};

export const trackEvent = (category, action, label) => {
  if (tracker) {
    tracker.event({ category, action, label });
  }
};

export const trackPageView = path => {
  if (tracker) {
    tracker.pageview(path);
  }
};

export const trackError = (category, action, error) => {
  const message = getMessageError(error);
  const labelGA = `${message} - ${error}`;
  // const labelGA = `${message} - ${getErrorData(error)}`;
  tracker.event({ category, action, label: labelGA });
};

export const set = item => {
  tracker.set(item);
};
