import React from 'react'
import Router from 'next/router'
import Link from 'next/link'
import { Container, Row, Col, Nav, NavItem, Button, Form, NavLink, Collapse,
         Navbar, NavbarToggler, NavbarBrand, Modal, ModalHeader, ModalBody,
         ModalFooter, ListGroup, ListGroupItem } from 'reactstrap'
import Package from '../../package'
import HeadCom from '../HeadCom'
import Header from '../Header'
import EngMenu from '../engStory/EngMenu'

export default class extends React.Component {

  static propTypes() {
    return {
      session: React.PropTypes.object.isRequired,
      providers: React.PropTypes.object.isRequired,
      children: React.PropTypes.object.isRequired,
      fluid: React.PropTypes.boolean,
      navmenu: React.PropTypes.boolean,
      signinBtn: React.PropTypes.boolean
    }
  }

  constructor(props) {
    super(props)
    this.state = {
      navOpen: false,
      modal: false,
      providers: null
    }
    this.toggleModal = this.toggleModal.bind(this)
  }

  async toggleModal(e) {
    if (e) e.preventDefault()

    // Save current URL so user is redirected back here after signing in
    if (this.state.modal !== true) {
    }

    this.setState({
      providers: this.state.providers,
      modal: !this.state.modal
    })
  }

  render() {
    const { nobaner } = this.props;
    let ban = '';
    if(!nobaner){
      ban = (<div className="text-light rounded-0 jumbot" style={{
        backgroundColor: 'rgba(73,155,234,1)',
        background: 'radial-gradient(ellipse at center, rgba(73,155,234,1) 0%, rgba(32,124,229,1) 100%)',
        boxShadow: 'inset 0 0 100px rgba(0,0,0,0.1)',
        padding: 5,
        marginLeft: '30px'
        }}>
        <Container className="mt-2 mb-2">
          <h1 className="display-2 mb-3" style={{fontWeight: 300}}>
            <span style={{fontWeight: 300}}>
              <span className="mr-3">▲</span>
              <br className="v-block d-sm-none"/>
              Từ điển
            </span>
            <br className="v-block d-lg-none"/> dịch đoạn
          </h1>
          <style jsx>{`
            .display-2  {
              border-radius: 0.2rem;
              text-shadow: 0 5px 10px rgba(0,0,0,0.3);
              color: rgba(255,255,255,0.9);
              font-size: 3em;
            }
            .lead {
              font-size: 2em;
              opacity: 0.7;
            }
            @media (max-width: 767px) {
              .display-2 {
                font-size: 2em;
                margin-bottom: 1em;
              }
              .lead {
                font-size: 1.5em;
              }
            }
          `}</style>
        </Container>
      </div>);
    }

    return (
      <React.Fragment>
        <Header/>
        <HeadCom/>

        <MainBody navmenu={this.props.navmenu} fluid={this.props.fluid} container={this.props.container}>
          {this.props.children}
        </MainBody>
        <EngMenu/>
        <Container fluid={this.props.fluid}>
          <hr className="mt-3"/>
          <p className="text-muted small">
            <span> Email </span>
            <a href="mailto:tudiendich@gmail.com">tudiendich@gmail.com</a>
            .
            <span className="ml-2">&copy; {new Date().getYear() + 1900}.</span>
          </p>
        </Container>
      </React.Fragment>
    )
  }
}

export class MainBody extends React.Component {
  render() {
    if (this.props.container === false) {
      return (
        <React.Fragment>
          {this.props.children}
        </React.Fragment>
      )
    } else if (this.props.navmenu === false) {
      return (
        <Container fluid={this.props.fluid} style={{marginTop: '1em'}}>
          {this.props.children}
        </Container>
      )
    } else {
      return (
        <Container fluid={this.props.fluid} style={{marginTop: '1em'}}>
          <Col xs="12" md="9" lg="10">
            {this.props.children}
          </Col>
        </Container>
      )
    }
  }
}
