import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import * as GA from '../../utils/analystics';
import{
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Modal,
  ModalHeader,
  ModalBody,
} from 'reactstrap';
import { chuanHoaChinneseNoSign, getSoundFile } from '../../utils/utils';
import TextareaAutosize from 'react-textarea-autosize';

const BreakLine = styled.div`
  display:none;
  display:block;
  width:100%;
  height:0;
`;
const AudioD = styled.audio`
  max-width: 220px;
  padding: 2px 2px;
`;
const BigText = styled.span`
  font-family: 'KaiTi', "Hiragino Sans GB", "Microsoft YaHei","WenQuanYi Micro Hei", sans-serif;
  font-size: 300%;
`;

const IframeI = styled.iframe`
  width: 100%;
  line-height: 1;
  border-radius: 0.2rem;
  min-height: 400px;
`;


function getDisplayHanViet(mean) {
  if(mean) {
    mean = mean.split(',');
    if (mean.length === 0) {
      mean = '';
    } else if (mean.length === 1) {
      mean = mean[0];
    } else {
      mean = `${mean[0]} ...`;
    }
   }
   return mean;
}

export default class ChuHan extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modal: false,
      nghiaViet: this.props.nghiaViet,
      bacKinh: this.props.bacKinh,
    };
    this.onHtmlChangeNghiaViet = this.onHtmlChangeNghiaViet.bind(this);
    this.nghiaButtonClick = this.nghiaButtonClick.bind(this);
    this.toggle = this.toggle.bind(this);
  }

  onHtmlChangeNghiaViet(e) {
    let nghiaViet = e.target.value;

    if(nghiaViet.indexOf('\n')>= 0) {
      this.setState({modal: !this.state.modal});
    } else {
      this.setState({ nghiaViet: nghiaViet });
      this.props.updateNghia(this.props.id, nghiaViet);
    }
  }

  toggle() {
    this.setState({
      modal: !this.state.modal,
    });
    GA.trackEvent('ChuHan', 'Han', this.props.chinesse);
  }

  nghiaButtonClick(nghia) {
    this.setState({ nghiaViet: nghia});
    this.props.updateNghia(this.props.id, nghia);
    GA.trackEvent('ChuHan', 'BacKinh', this.props.bacKinh);
  }

  render() {
    try {
      let {
        id,
        chinesse,
        hanViet,
        bacKinh,
        nghiaViet,
        dot,
      } = this.props;
      // console.log(chinesse, bacKinh);

      if(dot) {
        nghiaViet = `${nghiaViet}${dot} `;
      }

      let {nghias} = this.props;

      const { modal} = this.state;

      if (chinesse === '\n') {
        return (<BreakLine />);
      }
      if (chinesse.includes('.mp3')) {
        return (<AudioD className="no-print" controls loop src={`https://tv.nhidonghocphat.com/${chinesse}`} type="audio/mp3" />);
      }
      if (chinesse === '#B') {
        return ('');
      }

      let nghiaButtons = '', i = 0;
      if (nghias){
        nghias = `${nghias},${hanViet}`;

        nghiaButtons = nghias.split(',').map(item => {
          return (
            <button key={i++} className="btnBacKinh" onClick={() => this.nghiaButtonClick(item)}>{item}</button>
          );
        });
      }

      return (
        <div>
          <Card key={`${id}`} className="chuHanPrint"  style={{ borderLeftWidth: 0, borderRightWidth: 0 }}>
            <CardBody style={{ padding: 0 }}>
              <CardHeader style={{ padding: 0 }}>
                <button className="btnChuHan" id={`btnChuHan${id}`} onClick={this.toggle}>{chinesse}</button>
              </CardHeader>
              <Row className="lblGray" style={{ padding: 1, margin: 1, fontSize: 18 }}><small>{getDisplayHanViet(hanViet)}</small></Row>
              <Row style={{ padding: 1, margin: 1, fontSize: 18 }}><small>{nghiaViet}</small></Row>
            </CardBody>
          </Card>
          <Modal isOpen={modal} toggle={this.toggle} className={this.props.className}>
            <ModalHeader toggle={this.toggle}>{chuanHoaChinneseNoSign(chinesse)}</ModalHeader>
            <ModalBody>
              {/* <Col><BigText>{chinesse}</BigText></Col> */}
              Chọn lại nghĩa và chỉnh nếu cần
              {nghiaButtons}
              Nghĩa được chọn để dịch là (Enter để đóng)
              <TextareaAutosize
                className="txtAutoSize"
                id="inputChuHan"
                placeholder="Nghĩa Việt"
                value={nghiaViet}
                onChange={this.onHtmlChangeNghiaViet}
              /><br />
              <h3>Nghĩa chi tiết</h3>
              <IframeI
                src={`https://tv.nhidonghocphat.com/dictHtmls/${chuanHoaChinneseNoSign(chinesse)}.htm`}
              />
            </ModalBody>
          </Modal>
        </div>
      );
    } catch (e) {
      console.log('error', e);
      return (<Error title="Chữ Hán" content={e+''} />);
    }
  }
}

ChuHan.propTypes = {
  error: PropTypes.string,
  chinesse: PropTypes.string,
  chinessNoSign: PropTypes.string,
  hanViet: PropTypes.string,
  bacKinh: PropTypes.string,
  id: PropTypes.number,
  updateNghia: PropTypes.func,
};

ChuHan.defaultProps = {
  error: null,
  chinesse: '',
  chinessNoSign: '',
  hanViet: '',
  bacKinh: '',
  id: '',
};
